import React from "react";
import OraHtmlText from "eshop/modules/core/components/ui/OraHtmlText";
import PropTypes from "prop-types";

class DiscountInfo extends React.Component {

    getInfoDescription(){
	const discount = "Kwoty abonamentów uwzględniają rabaty za wyrażenie zgody na e-fakturę i terminowe płatności – 5 zł/mies. oraz zgód marketingowych – 5 zł/mies.";
	const discountSoft = "Kwoty abonamentów uwzględniają rabaty za wyrażenie zgody na e-fakturę i terminowe płatności – 5 zł/mies. oraz zgód marketingowych – 5 zł/mies., oraz rabat za łączenie usług w Orange – 20 zł/mies.";
        const defaultDescriptions={
            "discountInfo.VOICE.default" : discount ,
            "discountInfo.DATA.default" :  discount ,
            "discountInfo.VOICE.SOFT_BUNDLE_DUET" : discountSoft,
            "discountInfo.DATA.SOFT_BUNDLE_DUET" : discountSoft,
            "discountInfo.VOICE.SOFT_BUNDLE_COV" : discountSoft,
            "discountInfo.DATA.SOFT_BUNDLE_COV" : discountSoft,
        };
        const descriptionKey="discountInfo." + this.props.offerType + "." + (this.props.contractRole ? this.props.contractRole : "default");
        console.log("DiscountInfo descriptionKey", descriptionKey);
        return this.props.descriptions[descriptionKey] || defaultDescriptions[descriptionKey];
    }
    render() {
        return (
            <div className={this.props.className}>
                <div className="o-icon-text__icon">
                    <div className="g-icon g-icon--info g-icon--before g-bluef-c g-icon--xs-s" >
                    </div>
                </div>
                <div className="o-icon-text__text u-strong u-vertical-middle" ><OraHtmlText>{this.getInfoDescription()}</OraHtmlText></div>
            </div>
        )
    }
}

DiscountInfo.propTypes = {
    descriptions: PropTypes.objectOf(PropTypes.string).isRequired,
    contractRole: PropTypes.string,
    offerType: PropTypes.string.isRequired,
};
DiscountInfo.defaultProps = {
    className: "u-padding-s",
}



export default DiscountInfo;
